.Card {
  width: 12vw;
  height: 23vh;
  background-color: white;
  border-radius: 1vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

.Border {
  background: linear-gradient(to right, #99b3e6, #66ffcc);
  padding: 0.5rem;
  width: 12vw;
  position: relative;
  border-radius: 0.5vw;
  margin: 1vw;
}

.Header {
  background: linear-gradient(to right, #99b3e6, #66ffcc);
  color: white;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3vh;
  width: 12vw;
  border-top-right-radius: 0.5vw;
  border-top-left-radius: 0.5vw;
}

.Image {
  height: 3vh;
  width: 2vw;
}

.Info {
  color: black;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  margin-bottom: 2vh;
  height: 5vh;
}